@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,900);
h1{
	font-family: 'Roboto', sans-serif;
	font-size: 2em;
	font-weight: 700;
	color: #3C3C3C;
}

h2{
	color: #FF5A5F;
	font-size: 4vh;
	margin: 0;
	padding: 0;
	font-family: 'roboto';
}

h3{
	color: #FF5A5F;
	font-size: 2vh;
	margin: 0;
	padding: 0;
	font-family: 'roboto';
}

html{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	color: #333333;
	background-color: #F5F5F5;
}

body{
	width: 70%;
    position: absolute;
    left: 15%;
    height: 100%;
    margin: 0;
    background-color: #F5F5F5;
}

.cardContainer{
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
display: flex;
flex-direction: column;
justify-content: flex-start;
align-items: center
}

.card{
display: inline-block;
width: 70%;
height: 80vh;
position: relative;
background-color: #FFFFFF;
margin: 7vh 0 7vh 0;
box-shadow: 3px 3px 22px -4px rgba(0,0,0,0.5);
}

.welcome{
	text-align: center;
	color: #FF5A5F;
	font-size: 4vh;
	font-weight: 900;
	font-family: 'roboto';
}


.title{
	text-align: center;
	color: #FF5A5F;
	font-size: 4vh;
	font-weight: 400;
	font-family: 'roboto';
}


.background{
	position: absolute;
	height: 100vh;
	width: 100vw;
	background-color: rgba(0, 0, 0, 0.25);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

